/*     Header     */
.site-header {
  top: 0;
  left: 0;
  right: 0;
  background: #192231;
  color: #ffffff;
  padding: 1em;
  border-bottom: 3px solid #15b0f6;
  z-index: 4;
}
  .absolute-header {
    position: absolute;
  }
  .sticky-header {
    position: fixed;
    -webkit-animation-name: headerDrop;
            animation-name: headerDrop;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
  }
  .visible-header {
    position: fixed;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    -webkit-animation-name: headerRise;
            animation-name: headerRise;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
  }
  .site-header h1 {
    float: left;
  }
  .site-header span {
    color: #15b0f6;
  }


/*    NAVBAR    */
.nav-component {
  float: right;
  padding-top: 1rem;
  margin-right: 2rem;
}
  .nav-component ul{
    text-align: center;
    overflow: hidden;
    margin: 0;
    padding: 0;
  }
  .nav-component li {
    color: #ffffff;
    text-decoration: none;
    text-transform: uppercase;
    cursor: pointer;
    font-size: 1rem;
    display: inline-block;
    padding: 0 1rem 0 1rem;
  }
  .nav-component .menu-div {
    display: none;
  }
  .nav-component li:hover {
    font-weight: bold;
  }
  .resume-download a {
    text-decoration: none;
    color: white;
  }
  .resume-download img {
    padding-left: .4rem;
  }
  .resume-download:hover img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  .active-link {
    font-weight: bold;
    color: #15b0f6;
    padding-bottom: .2rem;
    border-bottom: 2px solid #15b0f6;
  }

/*    STICKY NAV    */
.sticky-nav {
  position: fixed;
  cursor: pointer;
  z-index: 4;
  top: 1rem;
  right: 1rem;
  background:#192231;
  border-radius: 50%;
  width: 3.5rem;
  height: 3.5rem;
}
  .sticky-nav span, 
  .sticky-nav span::after, 
  .sticky-nav span::before {
    position: absolute;
    width: 2rem;
    height: .2rem;
    background: #f4f4f4;
    border-radius: 100px;
  }
  .sticky-nav span {
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  .sticky-nav span::after {
    content: '';
    top: .5rem;
  }
  .sticky-nav span::before {
    content: '';
    top: -.5rem;
  }

/*    Wrapper for SVG effects. 
      Unfortunately filter breaks fixed positioning, so it's disabled
      Effect will work if placed in html    */
  /* .site-header {
    filter: url('#nav-detach-animation');
  } */


/*    Animations     */
@-webkit-keyframes headerDrop {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
@keyframes headerDrop {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@-webkit-keyframes headerRise {
  0% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
  }
}

@keyframes headerRise {
  0% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
  }
}


