/*    Modal    */
#demo-modal {
  position: fixed;
  margin: auto;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 10;
  background: #f4f4f4;
  padding: 1.5em;
  border: 1px solid black;
}
  .demo-content {
    min-width: 250px;
  }
  .hide-modal {
    display: none;
  }
  .show-modal {
    visibility: show;
    -webkit-animation: animateDrop 1.5s ease-out;
            animation: animateDrop 1.5s ease-out;
  }
  .demo-close {
    position: absolute;
    margin-top: .3em;
    margin-right: .5em;
    top: 0;
    right: 0;
    background: none;
    border: none;
    z-index: 10;
    font-size: 1.5em;
    font-weight: bold;
    font-family: sans-serif;
    display: block;
    color: darkred;
  }
    .demo-close:hover,
    .demo-close:focus {
      text-decoration: none;
      cursor: pointer;
    }


