  font: 16px/1.5 Arial, Helvetica, sans-serif;
  background-size: cover;
  background-color: #192231;
  max-width: 100vw;
  overflow-x: hidden;
}

@media only screen and (min-width: 1700px) {
  .container {
    width: 80%;
    margin: auto;
    overflow: hidden;
  }
  .App section {
    padding: 3rem;
  }
}
@media only screen and (max-width: 1699px) {
  .container {
    width: 95%;
    margin: auto;
    overflow: hidden;
  }
  .App section {
    padding: 3rem 0 3rem 0;
  }
}

.highlight {
  color: #15b0f6;
}

.section-header {
  position: relative;
  text-align: center;
  color: #15b0f6;
  padding: 1rem;
  background-color: #192231;
  font-size: 2rem;
  border-top: 3px solid #15b0f6;
  border-bottom: 3px solid #15b0f6;
}


/*    About me section    */
#about-me {
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  margin: auto;
  justify-content: center;
  overflow: hidden;
  background: linear-gradient(to right bottom, rgb(0, 0, 0, .4), rgb(0, 0, 0, .65)), 
    url(/My-First-Portfolio/static/media/bg-code.620dbc42.png);
  background-attachment: fixed;
  background-size: cover;
  color: white;
  text-shadow: .1rem .1rem black;
  padding-top: 4.5rem;
  padding-bottom: 1rem;
}
/* Optimize background image for faster loading on mobile*/
@media only screen and (max-width: 500px) {
  #my-portfolio {
    background: linear-gradient(to top, rgb(0, 105, 148, .3), rgb(0, 105, 148, .3)), 
    url(/My-First-Portfolio/static/media/bg-code-mobile.31d4f38e.png);
  }
}

.avatar-container {
  display: flex;
  align-self: center;
  text-align: center;
  padding: 1rem;
}
  .avatar-container img {
    border-radius: 50%;
    height: 100%;
    width: 100%;
    border: 1px solid black;
    -webkit-animation: animateAvatar 3s ease-out;
            animation: animateAvatar 3s ease-out;
  }

#about-me .description {
  align-self: center;
  text-align: center;
}
  #about-me .description h1 {
    margin-top: 4.5rem;
    padding-bottom: .5rem;
    font-size: 2em;
    border-bottom: 2px groove #15b0f6;
    -webkit-animation: animateDescription 3s ease-out;
            animation: animateDescription 3s ease-out;
  }
  @media only screen and (max-width: 1374px) {
    #about-me .description h1 {
      margin-top: 1rem;
    }
  }

#skill-icons {
  display: flex;
  flex-wrap: wrap;
  max-width: 1135px;
}
  /* Cascade the animation effect on the skill icons */
  .logo-box:nth-child(2) {
    -webkit-animation-delay: 3.3s;
            animation-delay: 3.3s;
  }
  .logo-box:nth-child(3) {
    -webkit-animation-delay: 3.6s;
            animation-delay: 3.6s;
  }
  .logo-box:nth-child(4) {
    -webkit-animation-delay: 3.9s;
            animation-delay: 3.9s;
  }
  .logo-box:nth-child(5) {
    -webkit-animation-delay: 4.2s;
            animation-delay: 4.2s;
  }
  .logo-box:nth-child(6) {
    -webkit-animation-delay: 4.5s;
            animation-delay: 4.5s;
  }
  .hidden {
    visibility: hidden;
  }
  .logo-box {
    display: flex;
    flex: 1 1;
    justify-content: center;
    text-align: center;
    margin: .5rem;
    -webkit-animation: animateSkills 1s ease-out 3s;
            animation: animateSkills 1s ease-out 3s;
  }
  .logo-box h3{
    min-width: 135px;
  }
    .logo-box img{
      max-width: 128px;
      max-height: 128px;
    }


/* About me animations */
@-webkit-keyframes animateAvatar {
  0% {
    -webkit-transform: translate(-200%) rotate(-360deg);
            transform: translate(-200%) rotate(-360deg);
  }
  100% {
    -webkit-transform: translate(0%) rotate(0deg);
            transform: translate(0%) rotate(0deg);
  }
}
@keyframes animateAvatar {
  0% {
    -webkit-transform: translate(-200%) rotate(-360deg);
            transform: translate(-200%) rotate(-360deg);
  }
  100% {
    -webkit-transform: translate(0%) rotate(0deg);
            transform: translate(0%) rotate(0deg);
  }
}

@-webkit-keyframes animateDescription {
  0% {
    -webkit-transform: translate(200%);
            transform: translate(200%);
  }
  100% {
    -webkit-transform: translate(0%);
            transform: translate(0%);
  }
}

@keyframes animateDescription {
  0% {
    -webkit-transform: translate(200%);
            transform: translate(200%);
  }
  100% {
    -webkit-transform: translate(0%);
            transform: translate(0%);
  }
}

@-webkit-keyframes animateSkills {
  0% {
    opacity: 0;
    -webkit-transform: scale(.1);
            transform: scale(.1);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes animateSkills {
  0% {
    opacity: 0;
    -webkit-transform: scale(.1);
            transform: scale(.1);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

/*    Portfolio section   */
#my-portfolio {
  padding: 0;
  background: linear-gradient(to top, rgb(0, 105, 148, .3), rgb(0, 105, 148, .3)), 
    url(/My-First-Portfolio/static/media/bg-code-dark.2a5b4298.jpg);
  background-attachment: fixed;
  background-size: cover;
}
/* Optimize background image for faster loading on mobile*/
@media only screen and (max-width: 500px) {
  #my-portfolio {
    background: linear-gradient(to top, rgb(0, 105, 148, .3), rgb(0, 105, 148, .3)), 
    url(/My-First-Portfolio/static/media/bg-code-dark-mobile.2da3120a.jpg);
  }
}
/*    Contact me section    */
#contact-me {
  background-color: #192231;
  color: white;
  min-height: 65vh;
}
  #contact-me .section-header {
    border: none;
    padding-top: 0;
  }
  #contact-me hr {
    border-color: #15b0f6;
    width: 80vw;
    margin: 0 auto 1.5rem auto;
  }

#contact-me .social{
  display: flex;
  margin: auto auto;
  justify-content: space-evenly;
  flex-wrap: wrap;
  max-width: 1190px;
}
.social div{
  flex: 1 1;
  background-color: #494e6b;
  text-align: center;
  max-width: 360px;
  max-height: 126px;
  min-width: 360px;
  border: 3px solid white;
  border-radius: 100px;
  margin: .5rem .1rem 1.5rem .1rem;
  transition: all 1s;
  overflow: hidden;
}

@media only screen and (max-width: 375px) {
  .social div{
    min-width: 300px !important;
  }
}

/*    Contact animation     */
.social .contact-animation {
  flex: none;
  z-index: 3;
  min-width: 120px !important;
  max-width: 125px;
  height: 126px;
  -webkit-animation: contactAnimation 1s;
          animation: contactAnimation 1s;
}
  .no-display {
    display: none;
  }

@-webkit-keyframes contactAnimation {
  0% { 
    opacity: 0; 
    -webkit-transform: translateY(-100vh) rotate(-360deg); 
            transform: translateY(-100vh) rotate(-360deg); 
  } 
  100% { 
    -webkit-transform: translateY(0) rotate(0deg); 
            transform: translateY(0) rotate(0deg); 
  } 
}

@keyframes contactAnimation {
  0% { 
    opacity: 0; 
    -webkit-transform: translateY(-100vh) rotate(-360deg); 
            transform: translateY(-100vh) rotate(-360deg); 
  } 
  100% { 
    -webkit-transform: translateY(0) rotate(0deg); 
            transform: translateY(0) rotate(0deg); 
  } 
}

  .social div:hover {
    transition: all .5s !important;
    -webkit-transform: scale(1.1) translateY(-1rem);
            transform: scale(1.1) translateY(-1rem);
    border: 3px solid #15b0f6 !important;
  }
  .social div:hover img { 
    border: 3px solid #15b0f6 !important;
  }

.social div a span,
.social #my-phone span {
  margin-top: 1.5rem;
}
.social a {
  text-decoration: none;
  color: white;
}
  .social div:hover a {
    color: #15b0f6 !important;
  }
  
.social div span {
  display: block;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  padding-right: 2rem;
  line-height: 2rem;
  margin-top: .5rem;
}
.social div span a,
.social div span p {
  text-align: center;
  word-wrap: break-word;
  font-size: 1rem;
  font-weight: normal;
}
#email-address {
  margin-bottom: .3rem;
}
#contact-me .social-icon {
  float: left;
  flex: 1 1;
  max-height: 120px;
  max-width: 120px;
  border: 2px solid white;
  background: white;
  width: 100%;
  border-radius: 50%;
}


/*    Site footer    */
#footer-copyright {
  color: #15b0f6;
  border-top: 2px groove #15b0f6;
  width: 95vw;
  margin: 1rem auto 0 auto;
  padding-top: 1.5rem;
  font-size: 2em;
  min-height: 10vh;
  text-align: center;
}

